<script>
export default {
  name: "users",

  data() {
    return {
      companies: [
        {
          businessName: "Balticum",
          category: "telecommunications",
          contact: "Virginijus Jurgelevičius",
          email: "info@balticum.lt",
          phone: "861112334",
          contactType: "phone",
          lastContact: "2019-12-14",
          progress: "Contacted: Ready for meeting",
          notes: "Feb 1st at 8:30.",
          edit: false
        },
        {
          businessName: "Maxima",
          category: "retail",
          contact: "Kristina Meidė",
          email: "k.meide@maxima.lt",
          phone: "861114434",
          contactType: "email",
          lastContact: "2020-01-15",
          progress: "Turned into customer",
          notes: "",
          edit: false
        },
        {
          businessName: "Rimi",
          category: "retail",
          contact: "Giedrius Bandzevičius",
          email: "gbandz@rimi.lt",
          phone: "88002900",
          contactType: "phone",
          lastContact: "2020-12-01",
          progress: "Contacted: Not Interested",
          notes: "",
          edit: false
        }
      ],

      firstInput: "",
      secondInput: "",
      thirdInput: "",
      fourthInput: "",
      fifthInput: "",
      sixthInput: "",
      seventhInput: "",
      eightInput: "",
      ninthInput: ""
    };
  },
  methods: {
    deleteRow: function(index) {
      document.querySelector("#customers .alert").style.opacity = "1";
      document.querySelector("#customers .alert").style.background = "red";
      document.querySelector("#customers .alert span").innerHTML =
        "Customer has been deleted.";
      this.companies.splice(index, 1);
    },
    addRow: function() {
      if (
        this.firstInput === "" ||
        this.secondInput === "" ||
        this.thirdInput === ""
      ) {
        document.querySelector("#addCustomer .alert").style.opacity = "1";
        document.querySelector("#addCustomer .alert").style.background = "red";
        document.querySelector("#addCustomer .alert span").innerHTML =
          "Please fill in all the first three fields.";
        return;
      } else {
        this.companies.push({
          businessName: this.firstInput,
          category: this.secondInput,
          contact: this.thirdInput,
          email: this.fourthInput,
          phone: this.fifthInput,
          contactType: this.sixthInput,
          lastContact: this.seventhInput,
          progress: this.eightInput,
          notes: this.ninthInput,
          edit: false
        });
        document.querySelector("#addCustomer .alert").style.opacity = "1";
        document.querySelector("#addCustomer .alert span").innerHTML =
          "Customer has been successfully added.";
        document.querySelector("#addCustomer .alert").style.background =
          "green";
        this.firstInput = "";
        this.secondInput = "";
        this.thirdInput = "";
        this.fourthInput = "";
        this.fifthInput = "";
        this.sixthInput = "";
        this.seventhInput = "";
        this.eightInput = "";
        this.ninthInput = "";
      }
    },

    close: function() {
      document.querySelector("#customers .alert").style.opacity = "0";
      document.querySelector("#addCustomer .alert").style.opacity = "0";
    },
    displayAddCustomer: function() {
      document.getElementById("addCustomer").style.display = "block";
      document.getElementById("customers").style.display = "none";
    },

    displayCustomers: function() {
      document.getElementById("customers").style.display = "block";
      document.getElementById("addCustomer").style.display = "none";
    },

    editRow: function(company) {
      this._originalCompany = Object.assign({}, company);
      company.edit = true;
    },
    cancelEditing: function(company) {
      Object.assign(company, this._originalCompany);
      company.edit = false;
    },
    save: function(company, index) {
      company.businessName = document.getElementsByClassName("newBussName")[
        index
      ].value;
      company.category = document.getElementsByClassName("newCat")[index].value;
      company.contact = document.getElementsByClassName("newCont")[index].value;
      document.querySelector(".alert").style.opacity = "1";
      document.querySelector(".alert span").innerHTML =
        "Customer has been updated.";
      document.querySelector(".alert").style.background = "green";
      company.edit = false;
    }
  }
};
</script>

